import React from 'react'

function Obj(props) {
    const { i } = props

    const isNormal = () => {
        // add here not normal inflate condition url and error will not inflate with normal inflate but with cutom inflate in render
        if(i[0] === 'url' || i[0] === 'error') return false
        return true
     }
     console.log(i)
    return (
        <>
            {i[0] === 'url' && <div className="object-item">{i[0]}  <br /> <a href={i[1]} target='blank'>{i[1].replace('https://shaarawy.fra1.digitaloceanspaces.com', 'space')}</a></div>}
            {isNormal() && <div className="object-item">{i[0]}  <br /> <span>{i[1]}</span></div>}

        </>
    )
}

export default Obj