import React, { useState } from 'react'
import Obj from './Obj'

function Log(props) {
    const { item } = props
    const [OpenedObj, setOpenedObj] = useState(false)
    return (
        <div className='logItem'>
            <div className="log-date">{getDate(item.date)}</div>
            <div className="log-msg">{item.msg}</div>
            <div className="obj">
                <div className="toggle-obj" onClick={() => { setOpenedObj(!OpenedObj) }}>deatils</div>
                {OpenedObj && <div className="log-obj">
                    {item.object && Object.entries(item.object).map((i, index) => (
                        <Obj key={'log_obj_' + index} i={i} />
                    ))}
                </div>}
            </div>
        </div>
    )


    function getDate(d) {
        const date = new Date(d)
        let year = date.getFullYear()
        let month = date.getMonth() + 1
        let day = date.getDate()
        let hour = date.getHours()
        let min = date.getMinutes()

        const formatNum = (num) => {
            if (num.toString().length === 1) return '0' + num
            return num
        }

        return `${year}-${formatNum(month)}-${formatNum(day)} ${formatNum(hour)}:${formatNum(min)}`
    }
}

export default Log